


















































html {
  height: 100%;
}

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  background-color: rgba(66, 185, 131, 0.74);
}

#app {
  color: #fff;
  max-width: 600px;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  text-align: center;
}

#app h1 {
  font-size: 40px;
}

#app p{
  font-size: 20px;
}
#app a {
  font-size: 18px;
  color: #fff;
  text-decoration: underline;
}

button {
  width: 100px;
  height: 50px;
  color: #ffffff;
  background: #8991dc;
  font-size: 17px;
  outline: none;
  border: none;
  padding: 5px;
  border-radius: 5px;
}

button:active {
  background-color: #7c82bd;
}



